import React, { useState } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import NavBar from './NavBar';
import HexGrid from './HexGrid';
import LiveData2 from './LiveData2';
import ProgressBar from './ProgressBar';
import GlobalStylesComponent from './GlobalStylesComponent';
import ProgressBar2 from './ProgressBar2';
import InvoiceGenerator from './InvoiceGenerator';

const lightTheme = {
  bodyBg: '#EAE0D5', // Champagne White, offering a base of quiet sophistication
  textColor: '#3E2723', // Deep Coffee, providing rich, elegant text
  linkColor: '#8C7B75', // Taupe Gray, for understated, luxurious links
  titleColor: '#DAA520', // Golden, making titles stand out with opulence
  dataColor: '#B0A8B9', // Lilac Gray, adding subtle sophistication to data presentation
  indicatorColor: '#C5B358', // Antique Gold, for classy, shining indicators
  progressIndicatorColor: '#B18F6A', // Bronze, offering a timeless metallic touch
  boxShadow: '0px 5px 15px rgba(46, 41, 42, 0.2)', // Enhanced Shadow, adding depth
  textShadow: '0px 2px 3px rgba(46, 41, 42, 0.1)', // Refined Glow, softly highlighting text
  navBg: '#F0EDE5', // Warm White, for a welcoming navigation area
  navTextColor: '#3E2723', // Deep Coffee, ensuring elegance and readability
  buttonBg: '#C19A6B', // French Beige, inviting interaction with warmth
  buttonHoverBg: '#A68B61', // Darker French Beige, for a distinguished hover effect
  tableEvenRowBg: '#F7F2EF', // Off White, ensuring a clean, airy table appearance
  tableHoverRowBg: '#EDE7E0', // Light Taupe, highlighting rows with grace
  tableBorderColor: '#DAD2C7', // Light Gray, softly defining borders
  tableHeaderBg: '#EAE0D5', // Champagne White, commanding attention in headers
  tableHeaderColor: '#3E2723', // Deep Coffee, for bold yet sophisticated header text

  // New invoice theme colors
  invoiceBg: '#F5F5F5', // Light gray background for invoice
  invoiceTextColor: '#333333', // Dark gray text color for invoice
  invoiceAccentColor: '#4CAF50', // Green accent color for important elements in invoice
  invoiceAttribute1: '#FFD700', // Gold color for additional attribute 1
  invoiceAttribute2: '#FF6347', // Tomato color for additional attribute 2
  invoiceAttribute3: '#6A5ACD', // SlateBlue color for additional attribute 3
};

const darkTheme = {
  bodyBg: '#1C1C1C', // Onyx Black, setting a backdrop of exclusivity
  textColor: '#E0D7C6', // Alabaster, making text read like silk on night
  linkColor: '#DAA520', // Golden, ensuring links catch the eye elegantly
  titleColor: '#B38728', // Bronze Gold, allowing titles to gleam with prestige
  dataColor: '#A79B8E', // Taupe, presenting data with an air of mystery
  indicatorColor: '#E59866', // Muted Orange, glowing indicators against the dark
  progressIndicatorColor: '#6B8E23', // Olive Drab, for standout progress bars
  boxShadow: '0px 10px 30px rgba(0, 0, 0, 0.5)', // Deep Shadow, making elements pop
  textShadow: '0px 2px 4px rgba(255, 255, 255, 0.15)', // Moonlight Glow, softly illuminating text
  navBg: '#242424', // Dark Charcoal, drawing in for navigation
  navTextColor: '#E0D7C6', // Alabaster, beacon-like text in the dark
  buttonBg: '#806D62', // Taupe, making buttons a pleasure to press
  buttonHoverBg: '#9C8577', // Lighter Taupe, tempting with luxury on hover
  tableEvenRowBg: '#2C2C2C', // Charcoal, seamlessly integrating table rows
  tableHoverRowBg: '#383838', // Slightly lighter Charcoal, subtly distinguishing rows
  tableBorderColor: '#444444', // Dark Gray, authoritatively framing with borders
  tableHeaderBg: '#1C1C1C', // Onyx Black, for deep, night-like headers
  tableHeaderColor: '#E0D7C6', // Alabaster, crisply contrasting against the dark

  // New invoice theme colors
  invoiceBg: '#333333', // Dark background for invoice
  invoiceTextColor: '#FFFFFF', // White text color for invoice
  invoiceAccentColor: '#1E90FF', // Orange accent color for important elements in invoice
  invoiceAttribute1: '#ADFF2F', // GreenYellow color for additional attribute 1
  invoiceAttribute2: '#00CED1', // DarkTurquoise color for additional attribute 2
  invoiceAttribute3: '#FFD700', // Gold color for additional attribute 3
};

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
`;

const App = () => {
  const [isLightMode, setIsLightMode] = useState(false);

  const toggleTheme = () => {
    setIsLightMode(prevMode => !prevMode);
  };

  return (
    <ThemeProvider theme={isLightMode ? lightTheme : darkTheme}>
      <AppContainer className={isLightMode ? 'light-mode' : 'dark-mode'}>
        <GlobalStylesComponent />
        <ProgressBar />
        <ProgressBar2 />

        <LiveData2 />
        <NavBar toggleTheme={toggleTheme} /> {/* Pass toggleTheme to NavBar */}

        <HexGrid />

        <InvoiceGenerator />

      </AppContainer>
    </ThemeProvider>
  );
};

export default App;

