import React, { useState, useEffect } from 'react';
import { jsPDF } from 'jspdf';
import {
  Container,
  Form,
  FormRow,
  FormLabel,
  Input,
  Button,
  ServicesList,
  ServiceItem,
  TotalAmount,
  InputContainer,
  LogoContainer,
  CompanyLogo,
  CompanyDetails,
  CompanyDetailsLabel,
  CompanyDetailsInput,
  Title,
  RemoveButton,
  FileInputContainer,
  FileInput,
  FileInputLabel,
} from './StyledComponents'; // Update the path as per your file structure

const InvoiceGenerator = () => {
  const [clientName, setClientName] = useState('');
  const [serviceName, setServiceName] = useState('');
  const [quantity, setQuantity] = useState(0);
  const [rate, setRate] = useState(0);
  const [services, setServices] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0);
  const [currency] = useState('USD');
  const [companyLogo, setCompanyLogo] = useState(null);
  const [companyDetails, setCompanyDetails] = useState({ name: '', address: '', contact: '' });
  const [isLoading, setIsLoading] = useState(false);
  const [feedbackMessage, setFeedbackMessage] = useState({ type: '', text: '' });
  const [shippingInput, setShippingInput] = useState('');
  const [salesTaxInput, setSalesTaxInput] = useState('');
  const [discountInput, setDiscountInput] = useState('');

  const commonClients = [
    "Company A",
    "Company B",
    "Company C",
    "Company X",
    "Company Y",
    "Company Z"
  ];

  const handleButtonClick = (value, type) => {
    if (type === 'quantity') {
      setQuantity(quantity + value);
    } else if (type === 'rate') {
      setRate(rate + value);
    }
  };

  const addService = () => {
    setIsLoading(true);
    const serviceTotal = quantity * rate;
    const newService = {
      name: serviceName,
      quantity: quantity,
      rate: rate,
      total: serviceTotal,
    };
    setServices([...services, newService]);
    setTotalAmount(totalAmount + serviceTotal);
    setServiceName('');
    setQuantity(0);
    setRate(0);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Service added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const generateInvoice = () => {
    setIsLoading(true);

    if (!clientName || services.length === 0) {
      setIsLoading(false);
      setFeedbackMessage({
        type: 'error',
        text: 'Please enter the client name and at least one service.'
      });
      setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);
      return;
    }

    const doc = new jsPDF({
      orientation: 'p',
      unit: 'mm',
      format: 'a4'
    });

    if (companyLogo) {
      doc.addImage(companyLogo, 'PNG', 20, 10, 50, 50);
    }

    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(10);
    doc.text(`${companyDetails.name}`, 20, 80);
    doc.text(`${companyDetails.address}`, 20, 85);
    doc.text(`${companyDetails.contact}`, 20, 90);

    // Invoice Title
    doc.setFont('Helvetica', 'bold');
    doc.setFontSize(24); // Larger font size
    doc.text('Invoice', 190, 20, null, null, 'right'); // Positioned in the top right-hand corner

    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(10);
    doc.text(`Date: ${new Date().toLocaleDateString()}`, 20, 115);
    doc.text(`Client: ${clientName}`, 20, 120);

    let yPos = 130;
    doc.setFont('Helvetica', 'bold');
    doc.text('Services:', 20, yPos);
    yPos += 7;

    doc.setFont('Helvetica', 'normal');
    services.forEach((service, index) => {
      doc.text(`${index + 1}. ${service.name}`, 20, yPos);
      doc.text(`Quantity: ${service.quantity}`, 110, yPos);
      doc.text(`Rate: $${service.rate.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 140, yPos);
      doc.text(`Total: $${service.total.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 170, yPos);
      yPos += 5;
    });

    yPos += 5;
    doc.setFont('Helvetica', 'bold');
    doc.text(`Total Amount: $${totalAmount.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 20, yPos);

    updateCompanyDetails(companyDetails);

    setIsLoading(false);
    setFeedbackMessage({
      type: 'success',
      text: 'Invoice generated successfully.'
    });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);
    doc.save('invoice.pdf');
  };

  const clearForm = () => {
    setClientName('');
    setServices([]);
    setTotalAmount(0);
    setCompanyLogo(null); // Clear the loaded logo image
    setFeedbackMessage({ type: 'success', text: 'Form cleared successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const addSalesTax = (taxRate) => {
    setIsLoading(true);
    const taxAmount = totalAmount * taxRate;
    const newService = {
      name: "Sales Tax",
      quantity: 1,
      rate: taxRate * 100,
      total: taxAmount,
    };
    setServices([...services, newService]);
    setTotalAmount(totalAmount + taxAmount);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Sales tax added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const addShipping = (shippingCost) => {
    setIsLoading(true);
    const newService = {
      name: "Shipping",
      quantity: 1,
      rate: shippingCost,
      total: shippingCost,
    };
    setServices([...services, newService]);
    setTotalAmount(totalAmount + shippingCost);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Shipping added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const applyDiscount = (amount) => {
    setIsLoading(true);
    setTotalAmount(totalAmount - amount);
    const newService = {
      name: `Discount (-$${amount})`,
      quantity: 1,
      rate: -amount,
      total: -amount,
    };
    setServices([...services, newService]);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Discount applied successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const removeService = (index) => {
    const updatedServices = services.filter((_, i) => i !== index);
    const updatedTotalAmount = updatedServices.reduce((acc, curr) => acc + curr.total, 0);
    setServices(updatedServices);
    setTotalAmount(updatedTotalAmount);
    setFeedbackMessage({ type: 'success', text: 'Service removed successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);
  };

  const quantityButtons = [1, 5, 10, 25, 100];
  const rateButtons = [1, 5, 10, 20, 100];

  // Load company details from local storage when the component mounts
  useEffect(() => {
    const storedCompanyDetails = localStorage.getItem('companyDetails');
    if (storedCompanyDetails) {
      setCompanyDetails(JSON.parse(storedCompanyDetails));
    }
  }, []); // Empty dependency array ensures this effect runs only once on mount

  // Update company details and save to local storage
  const updateCompanyDetails = (newDetails) => {
    setCompanyDetails(newDetails);
    localStorage.setItem('companyDetails', JSON.stringify(newDetails));
  };

  return (
    <Container>
      <Title>Quick Invoice</Title>
      <LogoContainer>
        {companyLogo && <CompanyLogo src={companyLogo} alt="Company Logo" />}
        <CompanyDetails>
          <CompanyDetailsLabel>Company Name:</CompanyDetailsLabel>
          <CompanyDetailsInput
            type="text"
            value={companyDetails.name}
            onChange={(e) => setCompanyDetails({ ...companyDetails, name: e.target.value })}
            placeholder="Enter company name"
          />
          <CompanyDetailsLabel>Address:</CompanyDetailsLabel>
          <CompanyDetailsInput
            type="text"
            value={companyDetails.address}
            onChange={(e) => setCompanyDetails({ ...companyDetails, address: e.target.value })}
            placeholder="Enter company address"
          />
          <CompanyDetailsLabel>Contact Info:</CompanyDetailsLabel>
          <CompanyDetailsInput
            type="text"
            value={companyDetails.contact}
            onChange={(e) => setCompanyDetails({ ...companyDetails, contact: e.target.value })}
            placeholder="Enter company contact info"
          />
        </CompanyDetails>
        <FileInputContainer>
          <FileInputLabel htmlFor="company-logo-upload">Upload Company Logo</FileInputLabel>
          <FileInput
            id="company-logo-upload"
            type="file"
            accept="image/*"
            onChange={(e) => setCompanyLogo(URL.createObjectURL(e.target.files[0]))}
          />
        </FileInputContainer>
      </LogoContainer>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormRow>
          <FormLabel>Client Name:</FormLabel>
          <Input
            type="text"
            value={clientName}
            onChange={(e) => setClientName(e.target.value)}
            placeholder="Enter client name"
          />
        </FormRow>
        <FormRow>
          <InputContainer>
            {commonClients.map((client, index) => (
              <Button key={index} onClick={() => setClientName(client)}>
                {client}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Service Name:</FormLabel>
          <Input
            type="text"
            value={serviceName}
            onChange={(e) => setServiceName(e.target.value)}
            placeholder="Enter service name"
          />
        </FormRow>
        <FormRow>
          <InputContainer>
            <Button onClick={() => setServiceName("Product Sale")}>Product Sale</Button>
            <Button onClick={() => setServiceName("Retail")}>Retail</Button>
            <Button onClick={() => setServiceName("Consultation")}>Consultation</Button>
            <Button onClick={() => setServiceName("Repair Service")}>Repair Service</Button>
            <Button onClick={() => setServiceName("Delivery")}>Delivery</Button>
            <Button onClick={() => setServiceName("Installation")}>Installation</Button>
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Quantity:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={quantity}
              onChange={(e) => setQuantity(parseInt(e.target.value))}
              placeholder="Enter quantity"
            />
            {quantityButtons.map((btnValue, index) => (
              <Button key={index} onClick={() => handleButtonClick(btnValue, 'quantity')}>
                +{btnValue}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Rate ($):</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={rate}
              onChange={(e) => setRate(parseInt(e.target.value))}
              placeholder="Enter rate"
            />
            {rateButtons.map((btnValue, index) => (
              <Button key={index} onClick={() => handleButtonClick(btnValue, 'rate')}>
                +${btnValue}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <Button onClick={addService}>Add Service</Button>
          <Button onClick={clearForm}>Clear Form</Button>
        </FormRow>
        <FormRow>
          <ServicesList>
            {services.map((service, index) => (
              <ServiceItem key={index}>
                {service.name} - Quantity: {service.quantity}, Rate: ${service.rate} {currency}, Total: ${service.total} {currency}
                <RemoveButton onClick={() => removeService(index)}>Remove</RemoveButton>
              </ServiceItem>
            ))}
          </ServicesList>
          <TotalAmount>Total Amount: ${(totalAmount.toFixed(2)).toLocaleString()} {currency}</TotalAmount>
          {isLoading && <div>Loading...</div>}
          {feedbackMessage.type === 'success' && <div style={{ color: 'green' }}>{feedbackMessage.text}</div>}
          {feedbackMessage.type === 'error' && <div style={{ color: 'red' }}>{feedbackMessage.text}</div>}
          <Button onClick={generateInvoice}>Generate Invoice</Button>
        </FormRow>
        <FormRow>
          <FormLabel>Add Shipping:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={shippingInput}
              onChange={(e) => setShippingInput(e.target.value)}
              placeholder="Enter shipping amount"
            />
            <Button onClick={() => addShipping(parseFloat(shippingInput))} style={{ width: 'auto' }}>
              Add
            </Button>
            {/* Quick buttons for common shipping costs */}
            {[5, 10, 15, 20].map((shippingCost, index) => (
              <Button key={index} onClick={() => addShipping(shippingCost)}>${shippingCost}</Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Add Sales Tax:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={salesTaxInput}
              onChange={(e) => setSalesTaxInput(e.target.value)}
              placeholder="Enter sales tax rate"
            />
            <Button onClick={() => addSalesTax(parseFloat(salesTaxInput) / 100)} style={{ width: 'auto' }}>
              Add
            </Button>
            {/* Quick buttons for common sales tax rates */}
            {[5, 7, 8.875, 9, 10].map((taxRate, index) => (
              <Button key={index} onClick={() => addSalesTax(taxRate / 100)}>{taxRate}%</Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Apply Discount:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={discountInput}
              onChange={(e) => setDiscountInput(e.target.value)}
              placeholder="Enter discount amount"
            />
            <Button onClick={() => applyDiscount(parseFloat(discountInput))} style={{ width: 'auto' }}>
              Apply
            </Button>
            {/* Quick buttons for common discount amounts */}
            {[5, 10, 15, 20].map((discountAmount, index) => (
              <Button key={index} onClick={() => applyDiscount(discountAmount)}>-${discountAmount}</Button>
            ))}
          </InputContainer>
        </FormRow>
      </Form>
    </Container>
  );
};

export default InvoiceGenerator;
